<template>
    <div class="newAddressWarp">
        <header>
            <router-link to="confiemorder">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <section>
                <span>添加地址</span>
            </section>
        </header>
        <section class="page_text_container">
            <section class="section_list">
                <span class="section_left">联系人</span>
                <section class="section_right">
                    <input type="text" class="input_style" placeholder="你的名字" v-model="name">
                    <div class="choose_sex">
                        <span class="choose_option">
                            <i class="el-icon-circle-check" style="margin-right: .3rem;font-size: .8rem;color:#4cd964 "></i>
                            <span>先生</span>
                        </span>
                        <span class="choose_option">
                            <i class="el-icon-circle-check"></i>
                            <span>女士</span>
                        </span>
                    </div>
                </section>
            </section>
            <section class="section_list">
                <span class="section_left">联系电话</span>
                <section class="section_right">
                    <div class="phone_add">
                        <input type="text" placeholder="你的手机号" class="input_style" v-model="phone">
                        <i class="el-icon-plus" style="color: blue"></i>
                    </div>
                    <input type="text" placeholder="备选电话" class="input_style phone_bk" v-model="phone_bk">
                </section>
            </section>
            <section class="section_list">
                <span class="section_left">送餐地址</span>
                <section class="section_right">
                    <!--<div class="choose_address" v-model="address" @click="goAddress"></div>-->
                    <input type="text"class="choose_address" placeholder="小区/写字楼/学校等" v-model="address" @click="goAddress">
                    <input type="text" placeholder="详细地址（如门牌号等）" class="input_style" v-model="address1">
                </section>
            </section>
            <section class="section_list">
                <span class="section_left">标签</span>
                <section class="section_right">
                        <input type="text" placeholder="无/家/学校/公司" class="input_style" v-model="tag">
                </section>
            </section>
        </section>
        <div class="footer" @click="addLoction">确定</div>
    </div>
</template>

<script>
    export default {
        name: "NewAddress",
        data(){
            return {
                name:'',
                address:'',
                address1:'',
                phone:'',
                phone_bk:"",//备注电话
                userId:'',//接收用户名id
                tag:'',//标签
                sex:1,//性别

                tag_type:'3',//标签类型，2:家，3:学校，4:公司
                poi_type:'0',//类型默认：0

            }
        },
        created(){
            this.userId = localStorage.getItem("user_id")
            this.address = this.$store.state.addressInfo.name;
            console.log(this.address)
            this.name = this.$store.state.userName;
            this.phone = this.$store.state.userPhone;
            this.phone_bk = this.$store.state.userPhone1;
            // localStorage.setItem("userNameId",name);
        },
        methods:{
            goAddress(){
                this.$store.state.userName=this.name;
                this.$store.state.userPhone=this.phone;
                this.$store.state.userPhone1=this.phone_bk;
                this.$router.push({path:'/searchAddress'})
            },
            addLoction(){
                this.axios.post("/v1/users/"+this.userId+"/addresses",{
                    user_id:this.userId,
                    name:this.name,//姓名
                    address:this.$store.state.addressInfo.address,//地址
                    address_detail:this.address1,
                    geohash:this.$store.state.addressInfo.geohash,
                    phone:this.phone,
                    tag:this.tag,
                    sex:this.sex,
                    poi_type:this.poi_type,
                    phone_bk:this.phone_bk,
                    tag_type:this.tag_type
                }).then(res=>{
                    console.log(res);
                    if (res.success==="添加地址成功") {
                        this.$router.push({path:'chooseAddress'})
                        this.$store.state.userName='';
                        // this.$store.state.addressInfo='';
                    }
                }).catch(err=>{

                })
            }
        }
    }
</script>

<style scoped>
    .newAddressWarp{
        padding-top: 1.95rem;
        /*background-color: azure;*/
    }
    header {
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    header>a{
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
        color: #fff;
    }
    header>section{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    header>section span{
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }
    section {
        display: block;
    }
    .page_text_container{
        background-color: #fff;
        padding: 0 .7rem;
    }
    .section_list{
        display: flex;
        border-bottom: .025rem solid #f5f5f5;
    }
    .section_left{
        font-size: .7rem;
        color: #333;
        flex: 2;
        line-height: 2.5rem;
    }
    .section_right{
        flex: 5;
    }
    .input_style{
        width: 100%;
        height: 2.5rem;
        font-size: .7rem;
        color: #999;
        outline: none;
        border: none;
    }
    .phone_bk{
        border-top: .025rem solid #f5f5f5;
    }
    .choose_sex{
        display: flex;
        line-height: 2.5rem;
        border-top: .025rem solid #f5f5f5;
    }
    .choose_option{
        font-size: .7rem;
        color: #333;
        display: flex;
        align-items: center;
        margin-right: .8rem;
    }
    .phone_add{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .choose_address{
        /*font-size: .7rem;*/
        /*color: #999;*/
        /*line-height: 2.5rem;*/
        /*border-bottom: .025rem solid #f5f5f5;*/
        width: 100%;
        height: 2.5rem;
        font-size: .7rem;
        color: #999;
        outline: none;
        border: none;
    }
    .footer{
        background-color: #4cd964;
        font-size: .7rem;
        color: #fff;
        text-align: center;
        margin: 0 .7rem;
        line-height: 1.8rem;
        border-radius: .2rem;
        margin-top: .6rem;
    }
</style>